import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";
import { useModal } from "mui-modal-provider";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import AuthService from "../services/AuthService";
import TokenService from "../services/TokenService";
import { TabContainer, TabBodyScrollable, TabTitle, TabParagraph, TabPrevButton, TabNextButton } from "./TabsComponents";
import FlexibleDialog from "./FlexibleDialog";
import config from "../config";
import { downloadLocalUrl } from "../libs/Misc";



function Tab02DownloadTemplate(props) {
  const { t } = useTranslation();
  const history = useHistory();
  const [ prevIsEnabled, ] = useState(true);
  //const [ nextIsEnabled, setNextIsEnabled ] = useState(() => props.job?.template?.downloaded ? props.job?.template?.downloaded : false);
  const [ nextIsEnabled, setNextIsEnabled ] = useState(() => !!(props.job?.downloadTemplate));

  const { showModal } = useModal();
  const openDialog = (props) => showModal(FlexibleDialog, props);

  const onPrev = () => {
    props.goto("prev");
  };

  const onNext = () => {
    props.goto("next");
  };

  const userIsAuthenticated = () => {
    if (!AuthService.getCurrentUser()) { // user is not authenticated
      openDialog({
        title: t("Please log in or register"),
        contentText: t("You need to be authenticated to proceed"),
        actions: [
          {
            text: t("Login"),
            closeModal: true,
            autoFocus: true,
            callback: () => {
              TokenService.set("redirect", props.tabId);
              history.push("/signin");
            },
          },
          {
            text: t("Register"),
            closeModal: true,
            callback: () => {
              TokenService.set("redirect", props.tabId);
              history.push("/signup");
            },
          },
          {
            text: t("Cancel"),
            closeModal: true,
            callback: () => props.goto("prev"),
          }
        ],
      });
      return false;
    }
    return true;
  }

  const onDownload = () => {
    if (userIsAuthenticated()) {
      downloadLocalUrl(config.data.templateDownloadUrl);
      setNextIsEnabled(true);
      props.setJob({...props.job, downloadTemplate: true, template: {...props.job?.template, downloaded: true}});
    }
  };

  return (
    <TabContainer>
      <TabBodyScrollable>
        <TabTitle>
          {t("Download")}
        </TabTitle>
        <TabParagraph>
          Scarica il modello Excel in cui potrai inserire i dati degli appalti, uno per riga.
        </TabParagraph>
        <TabParagraph>
          <Button onClick={onDownload} variant="contained" color="tertiary">
            {t("Download")} â¬‡
          </Button>
        </TabParagraph>
        {/* <br />
        <TabParagraph small>
          <input type="checkbox"></input>
          Clicca qui se preferisci la versione ODS (Open Document Format),
          nel caso che tu utilizzi LibreOffice anzichÃ© Microsoft Office.
        </TabParagraph> */}
      </TabBodyScrollable>

      <Grid container>
        <Grid item xs={6}>
          <TabPrevButton onPrev={onPrev} prevIsEnabled={prevIsEnabled}>
            {t("Back")}
          </TabPrevButton>
        </Grid>
        <Grid item xs={6}>
          <TabNextButton onNext={onNext} nextIsEnabled={nextIsEnabled}>
            {t("Continue")}
          </TabNextButton>
        </Grid>
      </Grid>

    </TabContainer>
  );
}
Tab02DownloadTemplate.propTypes = {
  goto: PropTypes.func.isRequired,
};
Tab02DownloadTemplate.defaultProps = {
};

export default React.memo(Tab02DownloadTemplate);
