import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";
import makeStyles from '@mui/styles/makeStyles';
import withStyles from '@mui/styles/withStyles';
import AppBar from '@mui/material/AppBar';
import Paper from '@mui/material/Paper';
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import TokenService from "../services/TokenService";
import JobService from "../services/JobService";
import Tab01Start from "./Tab01Start";
import Tab02DownloadTemplate from "./Tab02DownloadTemplate";
import Tab03FillData from "./Tab03FillData";
import Tab04Upload from "./Tab04Upload";
import Tab05Produce from "./Tab05Produce";
import Tab06DownloadDataset from "./Tab06DownloadDataset";
import Tab07Publish from "./Tab07Publish";
import Tab08Validation from "./Tab08Validation";
import Tab09Finished from "./Tab09Finished";
import config from "../config";



function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`nav-tab-${index}`}
      {...other}
    >
      <Box p={3}>
        {children}
      </Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

const a11yProps = (index) => {
  return {
    id: `nav-tab-${index}`,
    "aria-controls": `nav-tabpanel-${index}`
  };
}

const StyledTab = withStyles((theme) => ({
  root: {
    opacity: 0.8,
  },
}))(props => {
  return (
    <Tab {...props} />
  );
});

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    //backgroundColor: theme.palette.background.paper,
  },
  // tabs: {
  //   "& .MuiTabs-indicator": {
  //     //display: "none",
  //     backgroundColor: "orange",
  //   }
  // }
  tab: {
    backgroundColor: theme.palette.secondary.light,
  },
  tabIndicator: {
    backgroundColor: "MediumSeaGreen", //theme.palette.secondary.dark,
    height: 3,
  }

}));

const TabsPanel = () => {
  const classes = useStyles();
  const [ job, setJob ] = useState(() => TokenService.getJob());
  //const { job, setJob } = useContext(JobContext);
  //const [ job, setJob ] = useState(JobService.get());
  //const [ job, setJob ] = useState(TokenService.getJob());
  //const redirect = TokenService.getRedirect();
  //const [ tabId, setTabId ] = useState(TokenService.getJob());
  const { t } = useTranslation();

  useEffect(() => {
    const redirect = TokenService.get("redirect");
    if (redirect) {
      setJob({...job, tabId: redirect });
      TokenService.remove("redirect");
    }
  }, [job]);
  
  useEffect(() => { // to serialize job
    TokenService.setJob(job); // serialize locally, on local storage
    JobService.set(job); // serialize remotely, on server db
  }, [job]);

  function changeTab(id) {
    setJob({...job, tabId: id });
  }

  function forceTab(event, id) {
    if (config.ui.userCanForceTabChange) { // user can force tab change by clicking on app bar titles
      changeTab(id);
    }
  }
  
  function goto(where) {
    let id = where;
    if (typeof where === "string") {
      switch (where.toLowerCase()) {
        case "start":
          break;
        case "download template":
          break;
        case "fill your data":
          break;
        case "upload":
          break;
        case "produce":
          break;
        case "download dataset":
          break;
        case "publish":
          break;
        case "wait for validation":
          break;
        case "finish!":
          break;
        case "next":
          id = job ? job.tabId + 1 : 0;
          break;
        case "prev":
          id = job ? job.tabId - 1 : 0;
          break;
        default:
          console.error(`Unforeseen where specification in goto: ${where}`);
          return;
      }
    }
    changeTab(id);
  }

  if (!job?.tabId) job.tabId = 0;
  return (
    <div className={classes.root}>
      <AppBar elevation={0} position="fixed" style={{/*backgroundColor: "transparent",*/ top: 50}}>
        {/* <Tabs
          value={job.tabId}
          onChange={forceTab}
          indicatorColor="primary"
          // textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs"
          className={classes.tabs}
        > */}
        <Paper elevation={1} square>
          <Tabs
            value={job?.tabId}
            //indicatorColor="secondary"
            //textColor="secondary"
            variant="scrollable"
            scrollButtons="auto"
            onChange={forceTab}
            aria-label="current section"
            classes={{
              indicator: classes.tabIndicator
            }}
            className={classes.tab}
          >
            <StyledTab label={`${t("Start")} ðŸª„`} {...a11yProps(0)} />
            <StyledTab label={`${t("Download template")} â–¼`} {...a11yProps(1)} />
            <StyledTab label={`${t("Fill your data")} ðŸ–‹`} {...a11yProps(2)} />
            <StyledTab label={`${t("Upload")} â–²`} {...a11yProps(3)} />
            <StyledTab label={`${t("Produce")} ðŸ› ï¸`} {...a11yProps(4)} />
            <StyledTab label={`${t("Download dataset")} â¤µ`} {...a11yProps(5)} />
            <StyledTab label={`${t("Publish")} ðŸŒ`} {...a11yProps(6)} />
            <StyledTab label={`${t("Wait for validation")} ðŸŽ¯`} {...a11yProps(7)} />
            <StyledTab label={`${t("Finish!")} ðŸ`} {...a11yProps(8)} />
          </Tabs>
        </Paper>
      </AppBar>

      <>
        {(job?.tabId === 0) && (
          <TabPanel index={0} value={job.tabId}>
            <Tab01Start goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 1) && (
          <TabPanel index={1} value={job.tabId}>
            <Tab02DownloadTemplate goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 2) && (
          <TabPanel index={2} value={job.tabId}>
            <Tab03FillData goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 3) && (
          <TabPanel index={3} value={job.tabId}>
            <Tab04Upload goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 4) && (
          <TabPanel index={4} value={job.tabId}>
            <Tab05Produce goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 5) && (
          <TabPanel index={5} value={job.tabId}>
            <Tab06DownloadDataset goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 6) && (
          <TabPanel index={6} value={job.tabId}>
            <Tab07Publish goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 7) && (
          <TabPanel index={7} value={job.tabId}>
            <Tab08Validation goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
        {(job?.tabId === 8) && (
          <TabPanel index={8} value={job.tabId}>
            <Tab09Finished goto={goto} job={job} setJob={job => setJob(job)} />
          </TabPanel>
        )}
      </>
    </div>
  );
}

export { TabsPanel };